import './App.css';
import '@mantine/core/styles.css';
import { Table, MantineProvider } from '@mantine/core';
import React from 'react';
import axios from 'axios';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      lesson: [],
      taskList: []
    };
  }
  componentDidMount() {
    fetch('http://localhost:8000/api/v1/lessons')
      .then(response => response.json())
      .then(data => this.setState({ lesson: data }));
  };

  renderTableRows() {
    return this.state.lesson.map((row) => (
      <tr key={row.id}>
        <td>{row.time}</td>
        <td>{row.soundtrack}</td>
        <td>{row.bell}</td>
        <td>{row.library}</td>
      </tr>
    ));
  }

  handleSubmit = (event) => {
    event.preventDefault();
    const formData = new FormData(event.target);
    const data = Object.fromEntries(formData.entries());

    axios.post('http://localhost:8000/api/v1/lessons', data)
      .then(response => {
        this.setState(prevState => ({
          lesson: [...prevState.lesson, response.data]
        }));
      })
      .catch(error => console.error('Error:', error));
  }

  render() {
    return (
      <MantineProvider theme={{ colorScheme: 'dark' }}>
        <Table>
          <thead>
            <tr>
              <th>Čas</th>
              <th>Zvuková stopa</th>
              <th>Zvonček</th>
              <th>Knižnica</th>
            </tr>
          </thead>
          <tbody>
            {this.renderTableRows()}
            <tr>
              <td colSpan="4">
                <form onSubmit={this.handleSubmit}>
                  <input type="text" name="time" placeholder="Čas" required />
                  <input type="text" name="soundtrack" placeholder="Zvuková stopa" required />
                  <input type="text" name="bell" placeholder="Zvonček" required />
                  <input type="text" name="library" placeholder="Knižnica" required />
                  <button type="submit">Pridať lekciu</button>
                </form>
              </td>
            </tr>
          </tbody>
        </Table>
      </MantineProvider>
    );
  }
}

export default App;
